#app4{
}
@keyframes change {
    0%{
        background: brown;
    }
    100%{
        background:green;
    }
}
#app4 .circle{
    border: 1px solid red;
    width: 200px;
    height: 200px;
    border-radius: 50%;
   
}
#app4 .circle.active{
    animation: change 1s infinite alternate linear;
}